<template>
<div class="content__main">
    <el-row>
      <p><el-button type="text"></el-button></p>
      <el-button type="primary" @click="loadingFull =! loadingFull">loading</el-button>    
      <p v-vccc-loading.fullscreen="loadingFull" style="position: relative">
          <pre>
            update VNode 
             VNode 
            
             
            ()
          </pre>
      </p>
      <el-button type="primary" @click="loading = !loading">loading</el-button>
      <p v-vccc-loading="loading" style="position: relative">
        <pre>
          bind
          
          inserted 
          ()
        </pre>
      </p>
      <p><el-button type="text"></el-button></p>
      <div class="markdown-body" v-html="compiledMarkdown"></div>
    </el-row>
  </div>
</template>
<script>
import marked from 'marked';
import highlight from './../../static/js/highlight.min.js'
var rendererMD = new marked.Renderer();
marked.setOptions({
  renderer: rendererMD,
  gfm: true,
  tables: true,
  breaks: false,
  pedantic: false,
  sanitize: false,
  smartLists: true,
  smartypants: false,
  highlight: function(code) {
    return highlight.highlightAuto(code).value
  }
})
export default{
  data() {
    return{
      loadingFull: false,
      loading: false,
      code: '### markDown ```abcd```',
    }
  },
  computed: {
    compiledMarkdown: function(){
      return marked(this.code, {sanitize: true});
    }
  },
  methods:{
    handleLoadFull(){
      this.loadingFull = !this.loadingFull;
    }
  },
}
</script>




